export default {
	data() {
		return {
			auth_time:60,
			type: 0,
			bottomText: ['没有账号，去注册', '已有账号，去登陆'],
			getCodeText: '获取验证码',
			getOver: false,
			loginLoading:false,
			registerLoading:false,
			loginData: {
				account: '',
				l_password: ''
			},
			registerData: {
				phone: '',
				code: '',
				r_password: ''
			},
			providerList: [{
				providerName : '微信登录',
				icon:'../../static/wxIcon.png',
				id:'weixin'
			}]
		}
	},
	methods: {
		closeLogin(){
			this.$commonJs.navBack()
		},
		navTo(url) {
			this.$commonJs.navTo(url)
		},
		getCode() {
			var Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				}
			];
			let valLoginRes = this.$validate.validate(this.registerData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.getCode();
			
		},
		getCode(){
			var that = this;
			this.$apiJs.getCode({
				phone:this.registerData.phone,
			}).then(res => {
				if(res){
					that.$commonJs.uniMsg('发送成功', 'success')
					var auth_timetimer = setInterval(() => {
						that.auth_time--;
						that.getCodeText = this.auth_time + '后获取';
						that.getOver = true;
						if (that.auth_time <= 0) {
							that.getCodeText = '获取验证码';
							that.getOver = false;
							clearInterval(auth_timetimer);
						}
					}, 1000);
				}
			})
		},
		// 账号密码登陆
		submitlogin() {
			var that=this;
			let Rules = [{
					name: 'account',
					type: 'required',
					errmsg: '请输入账号'
				},
				{
					name: 'l_password',
					type: 'required',
					errmsg: '请输入密码'
				},
			]
			let valLoginRes = this.$validate.validate(this.loginData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.passowrdLogin();
			// var params = {name: "similar", age: 18, password: "1515"};
			// this.loginLoading=true;
			// console.log(this.$md5.getSign(params));			
			// setTimeout(function() {
			// 	that.$commonJs.uniMsg('登录成功', 'success')
			// 	that.loginLoading=false;
			// 	// uni.redirectTo({
			// 	// 	url:''
			// 	// })
			// 	console.log('跳转首页');
			// }, 1000);
		},
		passowrdLogin(){
				var that=this;
				this.$apiJs.login({
					phone:this.loginData.account,
					password:this.loginData.l_password
				}).then(res => {
					console.log(res);
					if(res.accesstoken){
						uni.setStorageSync('accesstoken',res.accesstoken);
						this.loginLoading=true;
						this.registerLoading=false;
					}
				}).then(()=>{
					this.getUserInfo();
				})
		},
		submitRegister() {
			let Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				},
				// {
				// 	name: 'code',
				// 	type: 'required',
				// 	errmsg: '请输入验证码'
				// },
				// {
				// 	name: 'r_password',
				// 	type: 'required',
				// 	errmsg: '请输入密码'
				// },
			]
			let valLoginRes = this.$validate.validate(this.registerData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			this.phoneLogin();
		},
		phoneLogin(){
			var that=this;
			this.$apiJs.phoneLogin({
				phone:this.registerData.phone,
				code:this.registerData.code
			}).then(res => {
				console.log(res);
				if(res.accesstoken){
					// this.$commonJs.uniMsg('注册成功', 'success');
					uni.setStorageSync('accesstoken',res.accesstoken);
					this.registerLoading=true;
					this.loginLoading=false;
				}
			}).then(()=>{
					this.getUserInfo();
				})
		},
		getUserInfo(){
			var that = this;
			this.$apiJs.getUser({}).then(res => {
				if(res){
					console.log(res);
					uni.setStorageSync('userInfo',res);
					that.$store.dispatch('setUserInfoObj', res) // 存储vuex
				}
			}).then(()=>{
				setTimeout(function() {
					that.$commonJs.uniMsg('登录成功', 'success')
					that.loginLoading=false;
					uni.switchTab({
						url:'index/index'
					})
					console.log('跳转首页');
				}, 500);
			})
		},
		getAuth(){
			uni.getProvider({
			    service: 'oauth',
			    success: (result) => {
					console.log(JSON.stringify(result)); 
			        this.providerList = result.provider.map((value) => {
			            let providerName = '',icon='';
			            switch (value) {
			                case 'weixin':
			                    providerName = '微信登录'
								icon='../../static/wxIcon.png'
			                    break; 
			                case 'qq':
			                    providerName = 'QQ登录'
								icon='../../static/qqIcon.png'
			                    break;
			                case 'sinaweibo':
			                    providerName = '新浪微博登录'
								icon='../../static/wbIcon.png'
			                    break;
			                case 'xiaomi':
			                    providerName = '小米登录'
			                    break;
			                case 'alipay':
			                    providerName = '支付宝登录'
			                    break;
			                case 'baidu':
			                    providerName = '百度登录'
			                    break;
			                case 'toutiao':
			                    providerName = '头条登录'
			                    break;
			                case 'apple':
			                    providerName = '苹果登录'
			                    break;
			            }
			            return {
			                name: providerName,
							icon:icon,
			                id: value
			            }
			        });
			
			    },
			    fail: (error) => {
			        console.log('获取登录通道失败', error);
			    }
			});
		},
		// 第三方授权登陆
		authlogin(i) {
		    uni.login({
		        provider:i,
		        success: (res) => {
		            console.log('login success:', res);
		           uni.getUserInfo({
		           	provider:i,
		           	success: (result) => {
		           		console.log('getUserInfo success', result.userInfo);
		           		this.$apiJs.weixinLogin(result.userInfo).then(res => {
							console.log(res);
							if(res.accesstoken){ 
								// this.$commonJs.uniMsg('注册成功', 'success');
								uni.setStorageSync('accesstoken',res.accesstoken);
							}
						}).then(()=>{
							this.getUserInfo();
						})
		           	}, 
		           	fail: (error) => {
		           		console.log('getUserInfo fail', error);
		           		let content = error.errMsg;
		           		if (~content.indexOf('uni.login')) {
		           			content = '请在登录页面完成登录操作';
		           		}







		           	}
		           });
		        },
		        fail: (err) => {
		            console.log('login fail:', err);
		        }
		    });
		}
	},
	mounted() {

		this.getAuth();

	}
}
